.feed-liked {
    margin-top: 1vh;
    height: 83vh;
    background-color: #32325d;
    border-radius: 1.5vh;
    text-align: left
}

.grid-liked{
    height: 71vh;
    width: 37vw;
    margin-top: 2vh;
    margin-bottom: 2vh;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: auto auto auto ;
    grid-gap: 0.5vw;
    overflow-y: scroll;
    border-radius: 5px;
}

.grid-liked::-webkit-scrollbar {
    display: none;
}

.imgContainer-liked {
    width: 12vw;
    height: 12vw;
    position: relative;
}

.img-liked {
    position: absolute;
    left: -1000%;
    right: -1000%;
    top: -1000%;
    bottom: -1000%;
    margin: auto;
    min-height: 100%;
    min-width: 100%;
    border-radius: 5px;
}

.noneLiked {
    width: 81%;
    height: 93%;
    margin-top: 3.5%;
    margin-bottom: 3.5%;
    margin-left: 9.5%;
    margin-right: 4.75%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}